#wordmark,
#university-of-connecticut,
#site-title,
#super-title {
    text-rendering: geometricPrecision;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(opacity=99)";
    filter: progid: DXImageTransform.Microsoft.Alpha(opacity=99);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

#uconn-banner article,
#uconn-banner aside,
#uconn-banner details,
#uconn-banner figcaption,
#uconn-banner figure,
#uconn-banner footer,
#uconn-banner header,
#uconn-banner hgroup,
#uconn-banner nav,
#uconn-banner section,
#uconn-banner summary {
    display: block
}

#uconn-banner audio,
#uconn-banner canvas,
#uconn-banner video {
    display: inline-block
}

#uconn-banner audio:not([controls]) {
    display: none;
    height: 0
}

#uconn-banner [hidden],
#uconn-banner template {
    display: none
}

#uconn-banner html {
    background: #fff;
    color: #000;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%
}

#uconn-banner html,
#uconn-banner button,
#uconn-banner input,
#uconn-banner select,
#uconn-banner textarea {
    font-family: sans-serif
}

#uconn-banner body {
    margin: 0
}

#uconn-banner a {
    background: transparent
}

#uconn-banner a:focus {
    outline: thin dotted
}

#uconn-banner a:hover,
#uconn-banner a:active {
    outline: 0
}

#uconn-banner h1 {
    font-size: 2em;
    margin: 0.67em 0
}

#uconn-banner h2 {
    font-size: 1.5em;
    margin: 0.83em 0
}

#uconn-banner h3 {
    font-size: 1.17em;
    margin: 1em 0
}

#uconn-banner h4 {
    font-size: 1em;
    margin: 1.33em 0
}

#uconn-banner h5 {
    font-size: .83em;
    margin: 1.67em 0
}

#uconn-banner h6 {
    font-size: .75em;
    margin: 2.33em 0
}

#uconn-banner abbr[title] {
    border-bottom: 1px dotted
}

#uconn-banner b,
#uconn-banner strong {
    font-weight: bold
}

#uconn-banner dfn {
    font-style: italic
}

#uconn-banner mark {
    background: #ff0;
    color: #000
}

#uconn-banner code,
#uconn-banner kbd,
#uconn-banner pre,
#uconn-banner samp {
    font-family: monospace, serif;
    font-size: 1em
}

#uconn-banner pre {
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word
}

#uconn-banner q {
    quotes: "\201C" "\201D" "\2018" "\2019"
}

#uconn-banner q:before,
#uconn-banner q:after {
    content: '';
    content: none
}

#uconn-banner small {
    font-size: 80%
}

#uconn-banner sub,
#uconn-banner sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline
}

#uconn-banner sup {
    top: -0.5em
}

#uconn-banner sub {
    bottom: -0.25em
}

#uconn-banner img {
    border: 0
}

#uconn-banner svg:not(:root) {
    overflow: hidden
}

#uconn-banner figure {
    margin: 0
}

#uconn-banner fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em
}

#uconn-banner legend {
    border: 0;
    padding: 0;
    white-space: normal
}

#uconn-banner button,
#uconn-banner input,
#uconn-banner select,
#uconn-banner textarea {
    font-family: inherit;
    font-size: 100%;
    margin: 0;
    vertical-align: baseline
}

#uconn-banner button,
#uconn-banner input {
    line-height: normal
}

#uconn-banner button,
#uconn-banner select {
    text-transform: none
}

#uconn-banner button,
#uconn-banner html input[type="button"],
#uconn-banner input[type="reset"],
#uconn-banner input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer
}

#uconn-banner button[disabled],
#uconn-banner input[disabled] {
    cursor: default
}

#uconn-banner input[type="checkbox"],
#uconn-banner input[type="radio"] {
    box-sizing: border-box;
    padding: 0
}

#uconn-banner input[type="search"] {
    -webkit-appearance: textfield;
    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    box-sizing: content-box
}

#uconn-banner input[type="search"]::-webkit-search-cancel-button,
#uconn-banner input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none
}

#uconn-banner button::-moz-focus-inner,
#uconn-banner input::-moz-focus-inner {
    border: 0;
    padding: 0
}

#uconn-banner textarea {
    overflow: auto;
    vertical-align: top
}

#uconn-banner table {
    border-collapse: collapse;
    border-spacing: 0
}

#uconn-banner a:link,
#uconn-banner a:hover,
#uconn-banner a:visited {
    text-decoration: none
}

