#uconn-banner [class*="icon-"] {
    font-family: 'icons';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

#uconn-banner .icon-a-z:before {
    content: "\e600"
}

#uconn-banner .icon-search:before {
    content: "\e601"
}

#uconn-banner .row-container {
    width: 100%;
    margin: 0px auto;
    box-sizing: border-box;
    padding-left: 1em;
    padding-right: 1em
}

#uconn-banner .row-fluid {
    width: 100%;
    display: table
}

#uconn-banner .row-fluid>div {
    display: table-cell;
    vertical-align: middle;
    float: none
}

#home-link-container {
    width: 60%
}

#uconn-header-container {
    background-color: #000E2F;
    color: #fff
}

#uconn-header-container .btn {
    color: #c1e8f8
}

#uconn-header-container .row-fluid>div {
    height: 60px
}

#home-link {
    height: 20px;
    overflow: hidden
}

#home-link:hover {
    text-decoration: none;
    color: #eee
}

#home-link span {
    margin: auto 0;
    vertical-align: middle
}

#site-header {
    padding: 30px 0 15px 0
}

#site-header .row-fluid>div {
    width: 50%
}

#wordmark {
    color: #fff;
    font-family: "UConn", Arial, Helvetica, sans-serif;
    font-size: 32px;
    overflow: hidden;
    letter-spacing: 3px;
    text-transform: uppercase;
    padding: 0px 5px 0 0
}

#university-of-connecticut {
    font-family: "Proxima Nova", Arial, Helvetica, sans-serif;
    color: #9faab2;
    font-size: 18px;
    letter-spacing: .055em;
    word-spacing: .02em;
    padding: 1px 0 0 10px;
    font-weight: bold;
    font-style: normal;
    border-left: 2px solid #3f4760;
    line-height: normal;
    display: none
}

@media (min-width: 50em) {
    #university-of-connecticut {
        display: initial
    }
}

#uconn-banner #button-container {
    text-align: right
}

#uconn-banner #button-container>span {
    width: 32px;
    height: 32px;
    display: inline-block;
    margin: 0px 2px;
    padding: 0px;
    vertical-align: middle;
    white-space: nowrap
}

#uconn-banner #button-container .btn {
    display: block;
    height: 100%;
    width: 100%;
    font-size: 21px;
    text-align: center;
    padding: inherit;
    text-shadow: none;
    line-height: normal;
    vertical-align: auto
}

#uconn-banner #button-container .btn [class*="icon-"] {
    line-height: 32px
}

#uconn-banner #button-container .btn,
#uconn-banner #button-container .btn:hover,
#uconn-banner #button-container .btn:visited,
#uconn-banner #button-container .btn:focus {
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    background-color: #1c3173;
    border: 1px solid #1e2b51;
    -moz-transition: none;
    -o-transition: none;
    -webkit-transition: none;
    transition: none;
    background-position: 0% 0%
}

#uconn-banner #button-container .btn:hover {
    color: #fff
}

#site-title {
    margin: 0px !important;
    padding: 0px;
    line-height: inherit
}

#site-title a {
    padding: 0;
    margin: 0
}

#site-title a:link,
#site-title a:visited {
    font-family: "Proxima Nova";
    font-size: 24px;
    font-weight: 500;
    color: #0b1860
}

#super-title {
    font-size: 13px;
    margin: 0 !important;
    padding: 0
}

#super-title a:link,
#super-title a:visited {
    color: #868695;
    font-family: "Proxima Nova", Arial, Helvetica, sans-serif;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 5px
}

#search-container {
    text-align: right
}

#search-container #button-container .btn-primary {
    background: #e9e9e9
}

#site-search {
    margin: 0px
}

.ie7 #uconn-banner .row-fluid>div,
.lt-ie8 #uconn-banner .row-fluid>div {
    display: block;
    float: left
}

.ie7 #button-container,
.lt-ie8 #button-container {
    height: 46px !important
}

.ie7 .icon-a-z,
.lt-ie8 .icon-a-z {
    *zoom: expression(this.runtimeStyle['zoom']='1', this.innerHTML='&#xe600;')
}

.ie7 .icon-search,
.lt-ie8 .icon-search {
    *zoom: expression(this.runtimeStyle['zoom']='1', this.innerHTML='&#xe601;')
}

.lt-ie9 #uconn-banner .row-fluid {
    white-space: nowrap
}

.lt-ie9 #uconn-banner .row-fluid>div {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: inline-block
}

.lt-ie9 #button-container {
    width: 40%;
    padding-top: 14px
}

.lt-ie9 #home-link-container {
    line-height: 60px
}