    font-weight: 900;
    font-style: normal;
    font-stretch: normal;
}

@font-face {
    font-family: 'Proxima Nova';
    src: url('//comm-files-universityofconn.netdna-ssl.com/shared/fonts/proximanova_bold_macroman/ProximaNova-Bold-webfont.eot') format('eot'), url('//comm-files-universityofconn.netdna-ssl.com/shared/fonts/proximanova_bold_macroman/ProximaNova-Bold-webfont.woff') format('woff'), url('//comm-files-universityofconn.netdna-ssl.com/shared/fonts/proximanova_bold_macroman/proximanova-bold-webfont.woff2') format('ttf');
