    font-stretch: normal;
}

@font-face {
    font-family: 'Proxima Nova';
    src: url('//comm-files-universityofconn.netdna-ssl.com/shared/fonts/proximanova_regular_macroman/proximanova-regular-webfont.eot') format('eot'), url('//comm-files-universityofconn.netdna-ssl.com/shared/fonts/proximanova_regular_macroman/proximanova-regular-webfont.woff') format('woff'), url('//comm-files-universityofconn.netdna-ssl.com/shared/fonts/proximanova_regular_macroman/proximanova-regular-webfont.woff2') format('ttf');
