    font-stretch: normal;
}


/*Global styles*/

body {
    color: #000000;
    font-family: 'Proxima Nova', sans-serif;
    font-weight: 500;
}

h1 {
    font-family: 'Proxima Nova', sans-serif;
    font-weight: 900;
    line-height: 1em;
    font-size: 45px;
    font-size: 10vw;
}

h2 {
    font-family: 'Proxima Nova', sans-serif;
    font-weight: 700;
    font-size: 35px;
    font-size: 6vw;
}

.banner-row {
    display: flex;
    flex-direction: row;
}

#mainImg {
    width: 100%;
    background-image: url(../img/horsebarnsunset161113a158-webcrop.jpg);
    background-size: cover;
    background-position: center;
}

#mainImg img,
.expectations img {
    display: block;
    width: 50%;
    margin-top: 15px;
    margin-bottom: 20px;
}

#mapImg {
    width: 100%;
    background-image: url(../img/ct-map-grey.png);
    background-position: center;
    height: 700px;
    background-repeat: no-repeat;
}

nav {
    width: 80%;
    margin-top: 50px;
}

nav ul li a {
    width: 100%;
    margin-top: 3px;
}

.expectations {
    width: 100%;
}

.expectations img {
    margin: 0 auto;
    width: 50%;
}

.social img {
    width: 36px;
    height: 36px;
}

.facts,
.campuses {
    padding: 0 20px;
}

.facts ul {
    list-style-type: none;
    padding-left: 0px;
}

.grey-bg {
    background-color: #ebebeb;
    padding-top: 50px !important;
    padding-bottom: 50px !important;
}

.padding-top-bottom-50 {
    padding-top: 50px !important;
    padding-bottom: 50px !important;
}

#accordion {
    margin-top: 25px;
    width: 100%;
}

.white {
    color: #fff;
}

.uconn-blue {
    color: #000e2f;
}

.callout {
    font-size: 28px;
    line-height: 1.2em;
}

.white-title-bg {
    background-color: #fff;
    padding: 10px 15px;
}

.title-rule {
    border-bottom: solid 4px #000000;
    padding-bottom: 25px;
}

.display-block {
    display: block;
}

.margin-bottom-20 {
    margin-bottom: 20px;
}

.margin-top-25 {
    margin-top: 25px;
}

.margin-top-50 {
    margin-top: 50px;
}

.padding-top-50 {
    padding-top: 50px;
}

.padding-bottom-50 {
    padding-bottom: 50px;
}

.margin-bottom-50 {
    margin-bottom: 50px;
}

.margin-bottom-30 {
    margin-bottom: 30px;
}

.list-line-height {
    line-height: 1.5em;
    margin-top: 15px;
}

.btn {
    border-radius: 0px;
    padding: 10px 25px;
}

.btn-danger {
    background-color: #d33a2c;
    border: 3px solid #d33a2c;
    color: #ffffff !important;
}

.btn-danger:hover {
    background-color: #ffffff;
    color: #000 !important;
}

.btn-primary {
    background-color: #efefef;
    border: 1px solid #000e2f;
    color: #000e2f;
}

.btn-primary:hover {
    background-color: #000e2f;
    border: 1px solid #000e2f;
    color: #fff;
}

.uconn-blue-bg {
    background-color: #000e2f;
}

.red {
    color: #d33a2c;
}

.font-22 {
    font-size: 22px;
}

.card-header {
    background-color: #d33a2c;
    border-bottom-color: #d33a2c;
    padding: 10px;
}

.card {
    border-color: #d33a2c;
    margin-bottom: 10px;
    border-radius: 0px;
}

.card-header:first-child {
    border-radius: 0px;
}

.btn-link {
    color: #fff;
    font-weight: 700;
    font-size: 18px;
    width: 100%;
    text-align: left;
}

.btn-link:hover {
    color: #000e2f;
    text-decoration: none;
}

.btn-link:active {
    text-decoration: none;
}

.btn-padding {
    padding: 10px;
}

footer {
    margin: 30px 0px;
}

footer a {
    color: #ddd;
    font-size: 14px;
    display: line-block;
    margin: 10px;
}

footer a:hover {
    color: #ddd;
    text-decoration: none;
    border-bottom: 2px solid #ddd;
}

#home-link-container {
    flex: 1;
    padding-left: 24px;
}

#button-container {
    height: 36px;
    margin: auto 0;
    padding-right: 24px;
}


/*Mobile*/

@media (max-width: 576px) {
    body {
        font-size: 20px;
        /*font-size: 4vw;*/
    }
    h1 {
        font-size: 45px;
        font-size: 10vw;
    }
    h2 {
        font-size: 35px;
        font-size: 6vw;
    }
    footer a {
        display: block;
    }
    #desktop-only,
    #desktop-small {
        display: none;
    }
}

@media (min-width: 576px) {
    body {
        font-size: 20px;
        /*font-size: 3vw;*/
    }
    h1 {
        font-size: 60px;
        font-size: 10vw;
    }
    h2 {
        font-size: 35px;
        font-size: 6vw;
    }
    footer a {
        display: block;
    }
    #desktop-only,
    #desktop-small {
        display: none;
    }
}


